<template>
  <div v-if="isVisible" class="migration-popup-overlay">
    <div class="migration-popup">
      <div class="popup-header">
        <i class="fas fa-info-circle"></i>
        <h2>Important Notice!</h2>
      </div>
      
      <div class="popup-content">
        <div class="new-domain">
          We've moved to a new domain
          <a href="https://bitcat.fun" class="domain-link" target="_blank">
            BITCAT.FUN
          </a>
        </div>
        
        <div class="description">
          <p>This site is fully functional but frozen and new features are only updated on the new domain.</p>
          <p>For the latest updates and new features, please visit <strong>BITCAT.FUN</strong></p>
        </div>
        
        <div class="action-buttons">
          <button class="btn-stay" @click="stayOnCurrentSite">
            Stay Here
          </button>
          <button class="btn-redirect" @click="redirectToNewDomain">
            Go to BITCAT.FUN
          </button>
        </div>
        
        <div class="countdown-section">
          <span class="countdown-text">Automatic redirect in:</span>
          <span class="countdown-timer">{{ countdown }}s</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted } from 'vue'

const isVisible = ref(true)
const countdown = ref(40)
let countdownInterval = null

const emit = defineEmits(['close'])

onMounted(() => {
  startCountdown()
})

onUnmounted(() => {
  if (countdownInterval) {
    clearInterval(countdownInterval)
  }
})

const startCountdown = () => {
  countdownInterval = setInterval(() => {
    countdown.value--
    
    if (countdown.value <= 0) {
      redirectToNewDomain()
    }
  }, 1000)
}

const stayOnCurrentSite = () => {
  closePopup()
}

const redirectToNewDomain = () => {
  // Перенаправляем без target="_blank" как просил пользователь
  window.location.href = 'https://bitcat.fun'
}

const closePopup = () => {
  if (countdownInterval) {
    clearInterval(countdownInterval)
  }
  isVisible.value = false
  emit('close')
}
</script>

